export default {
  mounted() {},
  data() {
    return {
      authPanel: "Login",
      server: "http://locahost:1337/",
      isPwd: true,
      name: "",
      identifier: "",
      password: "",
      constants: {
        login: "Login",
        register: "Register"
      },
      status: "",
      authorized: false
    };
  },
  methods: {
    toggleAuth(authPanel) {
      this.authPanel = authPanel;
    },
    checkEmail(v) {
      const pattern = /^\w+([._-]?\w+)*@\w+([._-]?\w+)*(\.\w{2,5})+$/;
      return pattern.test(v);
    },
    processRegister() {
      this.$refs.name.validate();
      this.$refs.email.validate();
      this.$refs.password.validate();
      if (
        !this.$refs.name.hasError &&
        !this.$refs.email.hasError &&
        !this.$refs.password.hasError
      ) {
        const dataObj = {
          name: this.name,
          username: this.identifier,
          email: this.identifier,
          password: this.password
        };
        console.log("register ->", dataObj);
        this.status = "Account has been created, please proceed to login";
        this.authPanel = this.constants.login;
      }
    },
    processLogin() {
      this.$refs.email.validate();
      this.$refs.password.validate();
      if (!this.$refs.email.hasError && !this.$refs.password.hasError) {
        var dataObj = {
          identifier: this.identifier,
          password: this.password
        };
        console.log("login ->", dataObj);
        this.authorized = true;
      }
    },
    processFacebookLogin() {
      let url = this.server + "connect/facebook";
      window.location = url;
    },
    processGoogleLogin() {
      let url = this.server + "connect/facebook";
      window.location = url;
    }
  }
};
